body {
  background: url("http://cdn.wallpapersafari.com/13/6/Mpsg2b.jpg");
  background-size: 100% 110%;
  margin: 0px;
  font-family: "Ubuntu", sans-serif;
  min-height: 100vh;
}
h1,
h2,
h3,
h4,
h5,
h6,
a {
  margin: 0;
  padding: 0;
}
.login {
  margin: 0 auto;
  max-width: 500px;
}
.login-header {
  color: #fff;
  text-align: center;
  font-size: 250%;
}
.login-header h1 {
  text-shadow: 0px 5px 15px #000;
}

.login-form {
  border: 0.5px solid #fff;
  /* background:#4facff; */
  border-radius: 10px;
  box-shadow: 0px 0px 10px #000;
}
.login-form h3 {
  text-align: left;
  margin-left: 40px;
  color: #fff;
}
.login-form {
  box-sizing: border-box;
  padding-top: 10%;
  padding-bottom: 10%;
  margin: 5% auto;
  text-align: center;
}
.login input[type="text"],
.login input[type="password"],
.login input[type="email"] {
  max-width: 400px;
  width: 80%;
  line-height: 3em;
  font-family: "Ubuntu", sans-serif;
  margin: 1em 2em;
  border-radius: 5px;
  border: 2px solid #f2f2f2;
  outline: none;
  padding-left: 10px;
}
.login-form input[type="button"] {
  padding: 10px 14px;
  background: #fff;
  border: 1px solid #f2f2f2;
  border-radius: 20px;
  color: slategrey;
  text-transform: uppercase;
  font-family: "Ubuntu", sans-serif;
  cursor: pointer;
}
.sign-up {
  color: #f2f2f2;
  padding: 15px;
  cursor: pointer;
  text-decoration: underline;
}
.no-access {
  color: #e86850;
  margin: 20px 0px 20px -57%;
  text-decoration: underline;
  cursor: pointer;
}
.try-again {
  color: #f2f2f2;
  text-decoration: underline;
  cursor: pointer;
}

.login-button {
  margin: 15px;
}
/* .login-button:hover{
  background: slategrey;
  color: #fff;
} */

/*Media Querie*/
@media only screen and (min-width: 150px) and (max-width: 530px) {
  .login-form h3 {
    text-align: center;
    margin: 0;
  }
  .sign-up,
  .no-access {
    margin: 10px 0;
  }
  .login-button {
    margin-bottom: 10px;
  }
}

/* Rest of your existing CSS code */

.profile-image-container {
  text-align: center;
  margin-bottom: 15px;
}

.profile-image-label {
  display: block;
  font-size: 18px;
  color: #fff;
  margin-bottom: 10px;
}

.profile-image-preview {
  max-width: 200px;
  height: auto;
  margin-bottom: 10px;
}

.profile-image-input {
  display: none;
}

.profile-image-upload-btn {
  display: inline-block;
  padding: 6px 10px;
  background: #fff;
  border: 1px solid #f2f2f2;
  border-radius: 20px;
  color: slategrey;
  text-transform: uppercase;
  font-family: "Ubuntu", sans-serif;
  cursor: pointer;
}

